:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

p {
  margin: 0;
}

.search-form {
  width: 100vw;
  height: 40px;
  text-align: center;
  background-color: #5e61c7;
  justify-content: center;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
}

.search-form input {
  width: 25%;
  height: 30px;
  color: #55575c;
  border: 1px solid #55575c;
  border-right: none;
  border-radius: 3px 0 0 3px;
  outline: none;
  padding: 5px;
}

input:focus {
  color: #0f1227;
}

.search-form button {
  width: 60px;
  height: 30px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background: #c6c9c9;
  border: 1px solid #55575c;
  border-radius: 0 3px 3px 0;
  font-size: 15px;
}

img {
  object-fit: cover;
  width: 100%;
  height: 294px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.photo-card {
  transition: box-shadow 250ms var(--timing-function);
  transition: transform var(--animation-duration) var(--timing-function);
  flex-basis: calc(33.3333% - 20px);
  box-shadow: 1px 1px 1px rgba(0, 0, 0, .12);
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 1px 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.gallery__link {
  pointer-events: auto;
  transition: transform var(--animation-duration) var(--timing-function);
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  height: 60px;
  text-align: center;
  color: #000;
  justify-content: center;
  gap: 15px;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  display: flex;
}

